<template>
  <div>
    <div class="centerTitle">
      新建管理员
    </div>
    <div class="formCon">
      <div class="formItem">
        <div class="label">管理员姓名：</div>
        <div class="input">
          <el-input v-model="realName" placeholder="请输入"></el-input>
        </div>
      </div>
      <div class="formItem">
        <div class="label">手机号：</div>
        <div class="input">
          <el-input v-model="tel_number" placeholder="请输入"></el-input>
        </div>
      </div>
      <div class="formItem">
        <div class="label">权限分配：</div>
        <div class="input">
          <el-radio v-model="role" label="Super_Admin">超级管理员</el-radio>
          <el-radio v-model="role" label="Admin">管理员</el-radio>
          <el-radio v-model="role" label="User">普通用户</el-radio>
        </div>
      </div>
      <div class="formBtnGroup">
        <el-button type="primary" class="CoBtn" @click="saveForm()">保存</el-button>
        <el-button class="CoResetBtn" @click="goPrePage()">返回上一页</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import SYSTEM from 'admin/service/system-service.js'
  export default {
    name: "AddAdministrators",
    data(){
      return{
        realName : '',
        tel_number : '',
        role: 'Admin'
      }
    },
    methods:{
      saveForm(){
        let data = {
          'realName' : this.realName,
          'tel_number' : this.tel_number,
          'role' : this.role
        };
        SYSTEM.setUserAdmin(data)
          .then(res => {
            console.log('res',res);
            if(res.code === "0"){
              this.$message({
                type: 'success',
                message: '操作成功'
              });
              this.goPrePage();
            }
          })
      }
    }
  }
</script>

<style lang="css" scoped>
  .totalCon{
    width: 95%;
  }
</style>



