import  axios from 'axios'

const service = axios.create({
    // baseURL:'http://192.168.1.183:3000',
    baseURL:'http://www.beihaozhuan.com',
    // baseURL:'http://39.100.32.180',
    withCredentials:true,
    headers:{
        'Content-Type' : 'application/json;charset=utf-8',
        'Access-Control-Allow-Credentials': true
    }
});
const formService = axios.create({
    // baseURL:'http://192.168.1.183:3000',
    baseURL:'http://www.beihaozhuan.com',
    // baseURL:'http://39.100.32.180',
    withCredentials:true,
    headers:{
        'Content-Type': 'multipart/form-data',
        'Access-Control-Allow-Credentials': true
    }
});

const MUtil = {
    request(url,data={},method='post') {
        return new Promise((resolve, reject) => {
            const options = {
                url,
                method
            }
            if(method.toLowerCase() === 'get'){
                options.params = data
            }else {
                options.data = data
            }
            service(options)
                .then(res => {
                    console.log('返回数据',res)
                    resolve(res.data);
                })
                .catch(error => {
                    reject(error);
                    console.log('操作失败111',error)
                })
        });
    },
    //表单提交方法
    formRequest(url,data={},method='post'){
        return new Promise((resolve, reject) => {
            const options = {
                url,
                method
            }
            if(method.toLowerCase() === 'get'){
                options.params = data
            }else {
                options.data = data
            }
            formService(options)
                .then(res => {
                    console.log('返回数据',res)
                    resolve(res.data);
                })
                .catch(error => {
                    reject(error);
                    console.log('操作失败111',error)
                })
        });
    }
}

export default MUtil



